html {
    box-sizing: border-box;
}
*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    font-family: 'Montserrat', sans-serif;
    letter-spacing: 0.02em;
    // background-color: $main-color-bg;
    // color: $secondary-color-text;
}

ul {
    list-style: none;
}
a {
    text-decoration: none;
}
p, h1, h2, h3, h4, h5, h6 {
    margin: 0;
}
ul, ol { 
    margin: 0;
    padding-left: 0;
}
img {
    display: block;
    width: 100%;
}